// Import Library's Hook
import { useState } from "react";

// Import Components
import ModalVideo from "../../../Components/ModalVideo";

// Import module css
import styles from "./styles.module.scss";

const Poster = () => {
   const [isFullPara, setIsFullPara] = useState(false);
   const [isOpenModal, setIsOpenModal] = useState(false);

   const handleClickTrailer = () => {
      setIsOpenModal(!isOpenModal);
   };

   return (
      <>
         <div className={styles.moviePoster}>
            <div className={styles.background}></div>
            <div className={styles.container}>
               <div className={styles.wrapper}>
                  <div className={styles.movieImg}>
                     <div>
                        <img src="../images/movieBlack.jpg" alt="Black" />
                        <div
                           className={styles.btnTrailer}
                           onClick={handleClickTrailer}
                        >
                           <svg
                              viewBox="0 0 48 48"
                              xmlns="http://www.w3.org/2000/svg"
                           >
                              <g fill="none" fillRule="evenodd">
                                 <circle
                                    stroke="#FFF"
                                    strokeWidth="2"
                                    fillOpacity=".24"
                                    fill="#000"
                                    cx="24"
                                    cy="24"
                                    r="23"
                                 ></circle>
                                 <path
                                    d="M34.667 24.335c0 .515-.529.885-.529.885l-14.84 9.133c-1.08.704-1.965.182-1.965-1.153V15.467c0-1.338.884-1.856 1.968-1.153L34.14 23.45c-.002 0 .527.37.527.885Z"
                                    fill="#FFF"
                                    fillRule="nonzero"
                                 ></path>
                              </g>
                           </svg>
                        </div>
                        <ModalVideo
                           isOpen={isOpenModal}
                           onClose={handleClickTrailer}
                           url="https://www.youtube.com/embed/js7_OSmYDxg"
                        />
                     </div>
                  </div>
                  <div className={styles.movieDescription}>
                     <div className={styles.ageLimit}>13+</div>
                     <h1>Chin Binh Bo en 2: Wakanda Bt Dit</h1>
                     <h3>Ni dung</h3>
                     <div className={`${styles.text} ${styles.showMore}`}>
                        {isFullPara
                           ? `N hong Ramonda, Shuri, MBaku, Okoye v Dora Milaje
                        chin u  bo v quc gia ca h khi s can thip
                        ca cc th lc th gii sau ci cht ca Vua TChalla.
                        Khi ngi Wakanda c gng nm bt chng tip theo ca
                        h, cc anh hng phi hp tc vi nhau vi s gip 
                        ca War Dog Nakia v Everett Ross v to ra mt con
                        ng mi cho vng quc Wakanda.`
                           : `N hong Ramonda, Shuri, MBaku, Okoye v Dora Milaje
                        chin u  bo v quc gia ca h khi s can thip
                        ca cc th lc th gii sau ci cht ca Vua TChalla.`}
                        <span onClick={() => setIsFullPara(!isFullPara)}>
                           {isFullPara ? "Thu gn" : "...Xem thm"}
                        </span>
                     </div>
                     <div className={styles.anotherDescription}>
                        <div className={styles.date}>
                           <p>Ngy chiu</p>
                           <span>11/11/2022</span>
                        </div>
                        <div className={styles.type}>
                           <p>Th loi</p>
                           <span>
                              Khoa Hc Vin Tng, Phiu Lu, Hnh ng
                           </span>
                        </div>
                     </div>
                  </div>
               </div>
            </div>
         </div>
      </>
   );
};

export default Poster;
