import styles from "./styles.module.scss";
import { Swiper, SwiperSlide } from "swiper/react";
import { Navigation } from "swiper";
import "swiper/css";
import "swiper/css/navigation";
import MovieShceduleItem from "./MovieScheduleItem";
import { useState } from "react";

const days = [
   { dd: 25, text: "Hm nay" },
   { dd: 26, text: "Th ba" },
   { dd: 27, text: "Th t" },
   { dd: 28, text: "Th nm" },
   { dd: 29, text: "Th su" },
   { dd: 30, text: "Th by" },
   { dd: 1, text: "Ch nht" },
];

const MovieSchedule = ({ rb }) => {
   const [activeId, setActiveId] = useState();

   const handleClick = (movieScheduleId) => {
      setActiveId(movieScheduleId);
   };
   return (
      <>
         <div className={styles.movieSchedule}>
            <div>
               <Swiper
                  breakpoints={rb}
                  spaceBetween={2}
                  speed={300}
                  navigation={true}
                  modules={[Navigation]}
                  className="movieSchedule-slide"
               >
                  {days.map((item, index) => (
                     <SwiperSlide key={index} className="movieSchedule-item">
                        <MovieShceduleItem
                           day={item}
                           isActive={
                              item.dd === activeId ||
                              (item.dd === 25 && !activeId)
                           }
                           setActive={handleClick}
                        />
                     </SwiperSlide>
                  ))}
               </Swiper>
            </div>
         </div>
      </>
   );
};

export default MovieSchedule;
