// Import CustomHook & utils
import useBodyScroll from "../../hooks/useBodyScroll";
import getVideoId from "../../utils/EmbedVideo";

// Import moduel css
import styles from "./styles.module.scss";

const ModalVideo = ({ isOpen, onClose, url }) => {
   useBodyScroll(isOpen);

   const videoId = getVideoId(url);

   return (
      <>
         <div className={`${styles.popup} ${isOpen ? `${styles.active}` : ""}`}>
            <div className={styles.modal}>
               <button className={styles.close} onClick={() => onClose()}>
                  <svg
                     xmlns="http://www.w3.org/2000/svg"
                     fill="none"
                     viewBox="0 0 24 24"
                     strokeWidth="2"
                     stroke="currentColor"
                  >
                     <path
                        strokeLinecap="round"
                        strokeLinejoin="round"
                        d="M10 14l2-2m0 0l2-2m-2 2l-2-2m2 2l2 2m7-2a9 9 0 11-18 0 9 9 0 0118 0z"
                     ></path>
                  </svg>
               </button>
               <div className={styles.body}>
                  <div className={styles.video}>
                     {isOpen ? (
                        <iframe
                           id="my-iframe-id"
                           width="100%"
                           height="100%"
                           src={`https://www.youtube.com/embed/${videoId}?autoplay=1&enablejsapi=1&widgetid=11`}
                           title="YouTube video player"
                           frameBorder="0"
                           allow="accelerometer;autoplay;clipboard-write; encrypted-media; gyroscope; picture-in-picture; muted"
                           allowFullScreen
                        ></iframe>
                     ) : (
                        ""
                     )}
                  </div>
                  <div className={styles.text}>
                     <h1>Chin Binh Bo en 2: Wakanda Bt Dit</h1>
                     <span>- Khoa Hc Vin Tng, Phiu Lu, Hnh ng</span>
                     <p>
                        N hong Ramonda, Shuri, MBaku, Okoye v Dora Milaje
                        chin u  bo v quc gia ca h khi s can thip
                        ca cc th lc th gii sau ci cht ca Vua TChalla.
                        Khi ngi Wakanda c gng nm bt chng tip theo ca
                        h, cc anh hng phi hp tc vi nhau vi s gip 
                        ca War Dog Nakia v Everett Ross v to ra mt con
                        ng mi cho vng quc Wakanda.
                     </p>
                  </div>
               </div>
               <div className={styles.footer}>
                  <button onClick={() => onClose()}>ng</button>
               </div>
            </div>
            <div className={styles.overlay}></div>
         </div>
      </>
   );
};

export default ModalVideo;
